/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.qodef-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
					}
				}
			}
		}
	}
}

@include phone-landscape {
	
	.qodef-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px !important;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {

					}

					.button {
						margin: 0;
						text-align: left;
						float: left;
					}
				}

				.button {
					margin: 20px 0 42px 0;
					text-align: left;
					float: left;
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */